import React, { useRef, useEffect, useState } from 'react';
import type { InputRef } from 'antd';
import { Input, Select } from 'antd';
import storage from '@/utils/storage';
import {
  DB_HOME_SEARCH_TYPE,
  SearchTypeMap,
  defaultSearchType,
} from './constants';
import styles from './index.less';

const { Option } = Select;

const Search: React.FC = () => {
  const inputRef = useRef<InputRef>(null);
  const _searchType = storage.get(DB_HOME_SEARCH_TYPE) || defaultSearchType;
  const [searchType, setSearchType] = useState(_searchType);

  const handleFocusSearch = () => {
    inputRef!.current!.focus({ cursor: 'start' });
  };

  const handlePressEnter = (e: any) => {
    e.stopPropagation();
    window.open(SearchTypeMap[searchType].gotoUrl(e.target.value));
  };

  const handleChangeSearchType = (value: string) => {
    setSearchType(value);
    storage.set(DB_HOME_SEARCH_TYPE, value);
    handleFocusSearch();
  };

  useEffect(() => {
    handleFocusSearch();
  }, []);

  const selectBefore = () => {
    return (
      <Select
        defaultValue={searchType}
        bordered={false}
        size="large"
        onChange={handleChangeSearchType}
      >
        {Object.keys(SearchTypeMap).map(key => (
          <Option key={key} value={key}>{SearchTypeMap[key].name}</Option>
        ))}
      </Select>
    );
  };

  return (
    <Input
      ref={inputRef}
      size="large"
      bordered={false}
      className={styles.searchWrap}
      onPressEnter={handlePressEnter}
      placeholder="输入搜索内容"
      addonBefore={selectBefore()}
    />
  );
};

export default Search;
