import React from 'react';
import { dateFormat } from '@/utils/tool';
import styles from './index.less';

const Wallpaper: React.FC = () => {
  const imageUrl = `http://cn.bing.com/iod/1366/1024/${dateFormat(null, 'yyyyMMdd')}1600`;

  return (
    <div className={styles.wallpaper} style={{ backgroundImage: `url(${imageUrl})` }} />
  );
};

export default Wallpaper;
